import React from 'react';
import { connect } from 'dva';
import EvaluateLayout from '../../components/order/EvaluateLayout';
import {bindActionCreators} from 'redux';
import * as _actions from '../../actions/evaluate';
import styles from './evaluate.css';

function Evaluate({location, dispatch, evaluate, channelinfo,order,　fhome,actions}) {
  const {enterInChannel} = evaluate;
  const {channelEntered,handlePay} = actions;
  const orders = channelinfo.orders && channelinfo.orders.length > 0 ? channelinfo.orders : null;
  const lastOrder =  orders ? orders[orders.length - 1] : null;
  let orderTitle = lastOrder ? (lastOrder.orderSn + lastOrder.details[0].productName).slice(0, 30) : '';

  const elProps = {
    currentStep:　order.currentStep,
    channelInfo: channelinfo,
    recvMsgs: fhome.msglist || [],
    fpar: fhome.fpar || null,
    handlePay:function(){
      const title = orderTitle;
      handlePay({
        title: title,
        amount: lastOrder.orderAmount,
        optional: {
          'orderId': lastOrder ? lastOrder.orderId : ''
        },
        returnurl: location.href
      });
    }
  }
  const changeChannel = (channelId) => {
    const msg = {
      type: 'openChatChannel',
      channelId: channelId,
      leaveChannel: ''
    }
    fhome.fpar.sendCustomRequest(msg, function(error,rsp){
      if(error){
        console.error(error);
        return;
      }

      channelEntered();
      const users = rsp.users.filter(u=>u.onlineStatus == 1);
      fhome.fpar.receiveVideos(users);

    });
  }
  if(!enterInChannel && channelinfo && fhome){
    changeChannel(channelinfo.channelId);
  }

  return (
      <EvaluateLayout {...elProps}/>
  );
}

function mapStateToProps({evaluate,channelinfo, fhome,order}) {
  return {evaluate,channelinfo, fhome,order};
}

function mapDispatchToProps(dispatch){
  return {
    actions: bindActionCreators(_actions, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Evaluate);



