import React, {Component} from 'react';
import {Layout, Row, Card, Col, Button,Input, Icon, Modal, Popover, Steps,　Tabs } from 'antd';
import Files from '../anteroom/Files';
import MsgList from '../anteroom/Msglist';
import Sender from '../anteroom/Sender';
import styles from './EvaluateLayout.css';

const Cookie = require('js-cookie');
const {Header,Content} = Layout;
const Step = Steps.Step;
const　TabPane　=　Tabs.TabPane;

class EvaluateLayout extends Component{
  constructor(props){
    super(props);
    this.state = {
      newFiles: []
    };
  }

  componentDidMount = () => {
    if(this.refs　&&　this.refs.msgScroller){
      this.refs.msgScroller.scrollTop = this.refs.msgScroller.childNodes[0].clientHeight;
    }
  }

  onHandlePay = ()=>{
    this.props.handlePay();
  }

  render(){
    const that = this;
    const channelId = this.props.channelInfo ? this.props.channelInfo.channelId : null;
    const users = this.props.channelInfo ? this.props.channelInfo.users : [];
    const filelist = this.props.channelInfo ? this.props.channelInfo.filelist : [];
    const msglist = this.props.channelInfo ? this.props.channelInfo.msglist : [];
    // const recvMsgs = this.props.recvMsgs || [];
    const selfUserId = this.props.channelInfo ? this.props.channelInfo.selfUserId　:'';
    const orders = this.props.channelInfo ? this.props.channelInfo.orders : [];
    const lastOrder = orders ? orders[orders.length - 1] : null;
    const fpar = this.props.fpar;

    const chatProps = {
      selfUserId: selfUserId,
      users: users,
      orders: orders,
      msglist: msglist
    }

    const sendMessage = (msg) => {
      this.props.fpar && this.props.fpar.sendMessage(msg);
    }
    const senderProps = {
      channelId: channelId,
      sendMsg(msg){
        sendMessage(msg);
      },
      changeFiles(files){
        const msg = {
          type: 'uploadingfile',
          channelId: this.props.channelInfo ? this.props.channelInfo.channelId : '',
          file: files
        }
        fpar && fpar.sendCustomRequest(msg, function(error, rsp){
          if(error){
            console.error(error);
            return;
          }
        });
      }
    }

    const　OrderSteps　=　()　=>
      <div　style={{padding: '20px'}}>
        <Steps current={this.props.currentStep || 0}  style={{padding: '20px 0'}} direction="vertical">
          <Step title="意向订单" description=""/>
          <Step title="协商定价" description=""/>
          <Step title="支付服务费" description=""/>
          <Step title="开始法律服务" description=""/>
          <Step title="确认服务完成" description=""/>
          <Step title="评价我的服务" description=""/>
        </Steps>
        <div>
          <Card title="价格信息" bordered={false} style={{fontSize: '1.2em'}}>
            {Cookie.get('r') != 9 && lastOrder && lastOrder.priceStatus > 0?
              <div>
                <p>本订单律师报价为:</p>
                <p style={{fontSize:'1.5em'}}>￥{Number(lastOrder.orderAmountY).formatCurrency()}元</p>
                <Button size='large' style={{width:'8em',color:'#fff', backgroundColor:'orange', borderColor:'orange'}} onClick={this.onHandlePay}>去付款</Button>
              </div>
            :<div><p>律师尚未报价</p><p>请提交资料等待律师评估</p></div>}
          </Card>
        </div>
      </div>

    const filesProps = {
      channelId: channelId,
      fileList: filelist,
      changeFiles(files){
        // changeFileList(files);
        let upfiles = that.state.newFiles;
        let currentIndex = -1;
        upfiles.forEach((f, i)=>{
          if(f.uid == files.uid){
            currentIndex = i;
          }
        })
        if(currentIndex != -1){
          upfiles.splice(currentIndex, 1, files);
        }else{
          upfiles.splice(0,0,files);
        }
        that.setState({newFiles: upfiles});
        const msg = {
          type: 'uploadingfile',
          channelId: channelId,
          file: files
        }
        fpar && fpar.sendCustomRequest(msg, function(error, rsp){
          if(error){
            console.error(error);
            return;
          }
        });
      },
      openFilePage(fileId, page){

      }
    }

    const ChannelFiles = () =>
      <Card title="文件" bordered={false} style={{fontSize:'1.2em'}}>
        <Files {...filesProps}/>
      </Card>


    const　TabPages　=　()　=>
      <Tabs　defaultActiveKey="1">
        <TabPane　tab="订单进度"　key="1"><OrderSteps></OrderSteps></TabPane>
        <TabPane　tab="订单详情"　key="2">
          <ChannelFiles />
        </TabPane>
      </Tabs>

    return (
      <div className={styles.normal} style={{position:'fixed', bottom:0, top:'70px',maxWidth:'1000px', left:'0px',right:'0px',margin:'auto' }} >
        <Layout style={{height:'100%', overflow:'hidden'}}>
          {/*<Header style={{height: '48px', background:'white',textAlign:'center'}}>*/}
            {/*<span>协商定价</span>*/}
          {/*</Header>*/}
          <Content style={{background:'white',height:'100%',overflow:'hidden'}}>
            <Col md={18} sm={24} style={{position:'relative',height:'100%',overflow:'hidden',padding:'8px 0px 80px 8px'}}>
              <MsgList {...chatProps}></MsgList>
              <Sender {...senderProps} ></Sender>
            </Col>
            <Col md={6} xs={0}>
              <TabPages/>
            </Col>
          </Content>
        </Layout>

      </div>
    );
  }
}



export default EvaluateLayout;



